body,h1{margin:0;padding:0;font:14px/1.4 '微软雅黑'}html,body{width:100%;height:100%}.header{position:absolute;top:0;width:100%;height:50px;background-color:#333;line-height:50px;z-index:100}.header .logo{float:left;height:100%;padding:10px;box-sizing:border-box}.header .logo img{height:100%}.header .title{float:left;color:#fff;font-size:16px;line-height:50px}.stage{position:relative;width:100%;height:100%;overflow:hidden}.img-sec{width:100%;height:100%;background-color:#f5f5f5;-webkit-perspective:1800px;perspective:1800px}.img-sec .img-fig{position:absolute;width:260px;height:340px;margin:0;padding:20px 20px 40px;box-sizing:border-box;background-color:#fff;cursor:pointer;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;-webkit-transform-origin:0 50% 0;transform-origin:0 50% 0;-webkit-transition:left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;transition:left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;transition:transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;transition:transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out}.img-sec .img-fig.is-inverse{-webkit-transform:translate(260px) rotateY(180deg);transform:translate(260px) rotateY(180deg)}.img-sec .img-fig img{width:100%;height:260px}.img-sec figcaption{text-align:center}.img-sec figcaption .img-tit{margin:16px 0 0;color:#a7a0a2;font-size:16px;font-weight:inherit}.img-sec .img-back{position:absolute;top:0;left:0;width:100%;height:100%;line-height:1.6;text-align:left;color:#a7a0a2;padding:30px;box-sizing:border-box;background-color:#fff;-webkit-transform:rotateY(180deg) translateZ(1px);transform:rotateY(180deg) translateZ(1px);-webkit-backface-visibility:hidden;backface-visibility:hidden}



